*{
    margin: 0;padding: 0;
}
ul{
    list-style: none;
}
a{
    color: inherit;
    text-decoration: none;
}

.wrap{
    width:980px;
    margin: auto;
}
.header{
    display: flex;
}
.header h1{
    color: #fff;
    text-align: center;
}
.header .header_divider{
    height: 45px;
    margin: 10px 0;
    float: left;
    color: #fff;
}
.header .header_menu{
    float: left;
    margin-left: 10px;
    line-height: 60px;
    border-bottom: 0;
    background-color: transparent;
}
.header .header_menu a{
    color: #fff;
}

.main .wrap{
    border: 1px solid #e8e8e8;
}
.nav li{
    text-align: center;
}
.nav li:hover{
    background-color:#e6f7ff;
}
.list_item{
    padding: 15px 0 0 10px;
}
.reply_list .list_item .pic{
    width: 300px;
}


.ant-card-body .reply_item img{
    width:300px;
}

.userAvatar {
    display: block;
    margin: 10px auto;
    width: 100px;
    height: 100px;
    border-radius: 50px;
}
.userInfo {
    text-align: center;
}
.markdown-text img{
    width: 100%;
}
.about_txt img{
    width:300px;
}




